import * as React from 'react';
import {Column} from "../bootstrap/Column";
import {INavigator} from "../../infrastructure/Navigator";
import {ICognitoController} from "../../models/CognitoController";
import {NewPassword} from "./NewPassword";



export interface IRegisterProps {
    navigator: INavigator,
    cognitoController: ICognitoController
}

export interface IRegisterState {
    username: string,
    company: string,
    location: string,
    email:string,
    validInput: boolean,
    validEmail: boolean,
    showPasswordScreen: boolean
}

export class Register extends React.Component<IRegisterProps, IRegisterState> {

    constructor(props:IRegisterProps) {
        super(props);
        this.state = {
            username: "",
            email: "",
            company: "",
            location: "",
            validInput: false,
            validEmail: true,
            showPasswordScreen: false,
        }
    }

    render() {
        return this.state.showPasswordScreen
            ? <NewPassword
                navigator={this.props.navigator}
                username={this.state.username}
                cognitoController={this.props.cognitoController}
                email={this.state.email}
                company={this.state.company}
                location={this.state.location}
            />
            : this.register();
    }

    register() {
        return <div style={{paddingTop: '75px'}}>
            <Column md={4} mdOffset={4}>
                <div className='login-form'>
                    <div className="title">Onsight</div>
                    <div className="form-group ">
                        <input type="text" className="form-control" placeholder="Company" onChange={(e) => this.handleCompanyChange(e)}/>
                    </div>
                    <div className="form-group ">
                        <input type="text" className="form-control" placeholder="Location" onChange={(e) => this.handleLocationChange(e)}/>
                    </div>
                    <div className="form-group ">
                        <input type="text" className="form-control" placeholder="Username" onChange={(e) => this.handleUsernameChange(e)}/>
                    </div>
                    <div className="form-group ">
                        <input type="text" className="form-control" placeholder="Email" onChange={(e) => this.handleEmailChange(e)}/>
                        {this.checkEmailValid()
                            ? <i style={{color: 'green'}} className="fa fa-check"/>
                            : this.state.email != ''
                                ? <i className="fa fa-times"/>
                                : null
                        }
                    </div>
                    <button type="button" className="log-btn" disabled={!this.state.validInput} onClick={() => this.onSubmit()}>Submit</button>
                </div>
            </Column>
        </div>
    }

    private handleUsernameChange(event) {
        this.setState({username: event.target.value});
        console.log(this.props.cognitoController.userExists(event.target.value));
        this.updateFieldsFilled();
    }
    private handleEmailChange(event) {
        this.setState({email: event.target.value});
        this.updateFieldsFilled();

    }
    private handleCompanyChange(event) {
        this.setState({company: event.target.value});
        this.updateFieldsFilled();

    }
    private handleLocationChange(event) {
        this.setState({location: event.target.value});
        this.updateFieldsFilled();
    }

    private updateFieldsFilled():void {
        const allFieldsFilledStatus = this.state.company != '' && this.state.location != '' && this.state.email != '' && this.state.username != '';
        this.setState({validInput: allFieldsFilledStatus && this.checkEmailValid()});
    }

    private checkEmailValid():boolean {
        const emailValidationRegex = /\S+@\S+\.\S+/;
        return emailValidationRegex.test(this.state.email);
    }

    private async onSubmit() {
        this.setState({showPasswordScreen: true});
    }
}


