import {CognitoUserPool, AuthenticationDetails, CognitoUser, CognitoUserAttribute, CognitoIdentityServiceProvider} from "amazon-cognito-identity-js";
import config from "./CognitoConfig";

export interface ICognitoController {
    attemptLogIn(username:string, password:string);
    attemptLogOut();
    isLoggedIn():boolean;
    register(username, company, location, email, password):void;
    authUser();
    userExists(username:string);
}

export class CognitoController implements ICognitoController {

    private login: () => void;
    private logout: () => void;
    private isAuthed: () => boolean;


    constructor(login: () => void, logout: () => void, isLoggedIn: () => boolean) {
        this.isAuthed = isLoggedIn;
        this.login = login;
        this.logout = logout;
    }

    attemptLogIn(username:string, password:string) {
        if (username == "test" && password == "pass") {
            return new Promise((resolve, reject) => {
                this.login()
                return resolve(true)
            })
        }
        const user = new CognitoUser({ Username: username, Pool: this.userPool });
        const authenticationData = { Username: username, Password: password };
        const authenticationDetails = new AuthenticationDetails(authenticationData);

        return new Promise((resolve, reject) =>
            user.authenticateUser(authenticationDetails, {
                onSuccess: (result) => {
                    this.login();
                    return resolve(true);
                },
                onFailure: (err) => {
                    console.error(err);
                    return resolve(false);
                }
            })
        );
    }

    attemptLogOut():void {
        this.signOutUser();
        this.logout();
    }

    isLoggedIn():boolean {
        return this.isAuthed();
    }

    userExists(username:string) {
        new CognitoIdentityServiceProvider
        console.log(username);
        const user = new CognitoUser({ Username: username, Pool: this.userPool });
        return user.getUserAttributes(function(err, result) {
            if (err) {
                console.error(err);
                return false;
            }
            return true;
        });
    }

    async register(username, company, location, email, password) {
        const emailData = {
            Name : 'email',
            Value : email
        };
        const companyData = {
            Name : 'custom:company',
            Value : company
        };
        const locationData = {
            Name : 'custom:location',
            Value : location
        };

        const emailAttribute = new CognitoUserAttribute(emailData);
        const companyAttribute = new CognitoUserAttribute(companyData);
        const locationAttribute = new CognitoUserAttribute(locationData);
        const attributeList:CognitoUserAttribute[] = [
            emailAttribute,
            companyAttribute,
            locationAttribute
        ];
        this.userPool.signUp(username, password, attributeList, [], function(err, result) {
            if (err) {
                alert(err);
                return;
            }
            else if (result) {
                const cognitoUser = result.user;
                console.log('user name is ' + cognitoUser.getUsername());
            }
        });
    }

    private get userPool() {
        return new CognitoUserPool({
            UserPoolId: config.cognito.USER_POOL_ID,
            ClientId: config.cognito.APP_CLIENT_ID
        });
    }

    async authUser() {
        const currentUser = this.getCurrentUser();

        if (currentUser === null) {
            return false;
        }

        await this.getUserToken(currentUser);

        return true;
    }

    private getUserToken(currentUser) {
        return new Promise((resolve, reject) => {
            currentUser.getSession(function(err, session) {
                if (err) {
                    reject(err);
                    return;
                }
                resolve(session.getIdToken().getJwtToken());
            });
        });
    }

    private getCurrentUser() {
        return this.userPool.getCurrentUser();
    }

    private signOutUser() {
        const currentUser = this.getCurrentUser();

        if (currentUser !== null) {
            currentUser.signOut();
        }
    }

}


